import { Dialog, DialogActions, DialogContent, DialogTitle, TextField, Typography } from '@mui/material';
import React from 'react';
import { Controller, useForm } from 'react-hook-form';

import { SubmitBtn } from '../header/style';

export const HeaderForm = ({ open, handleClose }) => {
  const {
    control,
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  const onSubmit = (data) => {
    console.log(data);
    handleClose();
    reset();
  };

  return (
    <Dialog
      open={open}
      onClose={() => {
        handleClose();
        reset();
      }}
    >
      <DialogTitle>
        <Typography variant="h4">  </Typography>
      </DialogTitle>
      <DialogContent>
        <form onSubmit={handleSubmit(onSubmit)}>
          <TextField
            margin="dense"
            error={errors.name}
            helperText={errors.name && errors.name.message}
            label=" "
            fullWidth
            variant="standard"
            {...register('name', {
              required: {
                value: true,
                message: '',
              },
            })}
          />
          <TextField
            margin="dense"
            error={errors.phone}
            type="tel"
            helperText={errors.phone && errors.phone.message}
            label=" "
            fullWidth
            variant="standard"
            {...register('phone', {
              required: {
                value: true,
                message: '',
              },
              pattern: {
                value: /^(\+?7|8)\d{10}$/,
                message: '   ',
              },
            })}
          />
          <DialogActions>
            <SubmitBtn onClick={() => console.log(errors)} type="submit" variant="contained">
              
            </SubmitBtn>
          </DialogActions>
        </form>
      </DialogContent>
    </Dialog>
  );
};
